.LoadingTodo-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:24px ;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
    padding: 12px 0;
}

.LoadingTodo-Text{
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: bold;
}

.LoadingTodo-completeIcon, .LoadingTodo-deleteIcon{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    height: 40px;
    width: 40px;
}
.LoadingTodo-completeIcon{
    position: absolute;
    left:12px;
}

.LoadingTodo-deleteIcon{
    position: absolute;
    top: -24px;
    right: 0;
}


.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    background: linear-gradient(90deg, rgba(87, 45, 240, 0.562),
    rgba(58, 12, 165, 0.226));
    background-size: 200% 200%;
    animation: loadingAnimation 3 ease-in-out infinite;
}

@keyframes loadignAnimation{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}