
li{
    display: flex;
    justify-content: space-around;
    width: 29em;
    padding: 10px;
    margin-top: 15px;
    color: rgb(212, 29, 29);
    border: 1px solid #000;
    border-radius: 10px;
}
li > p{
    text-transform: capitalize;
    text-align: left;
    width: 100%;
    padding-left: 20px;
    color: rgb(255, 255, 255);
}
